body {
    background-image: linear-gradient(to top, #da1670, #ca0081, #af0096, #8600ac, #330bc1);
}

.cardContainer {
    margin-top: 10px;
    box-shadow: 3px 3px 3px rgb(5, 5, 5);
    /* border: 2px solid #000; */
    /* border-radius: 0.25em */
}

.card:hover {
    /* padding: 2px; */
    padding: 2px;
    cursor: pointer;
    background-image: linear-gradient(to left top, #facb06, #ed9514, #d26324, #ac352b, #7e082a);
}

.container-fluid,
.container {
    padding: 0;
}

.navbar-brand {
    font-family: 'Satisfy', cursive;
    font-size: 1em;
    font-weight: bolder;
}

i.fa {
    font-size: 1.2em;
    color: rgb(25, 2, 88);
    padding-right: 10px;
}

form,
input {
    margin-top: 10px;

}

input.btn {
    margin-right: 10px;
    width: 100px;
}

